import React, { useContext, useState } from 'react';
import Store from '../../store';
import { AppHistory } from '../../App';

export default (props, context) => {
  const { store, setContext } = useContext(Store);

  const [state, setState] = useState({
    error: false,
    username: '',
    password: ''
  });
  const inputChanged = ({ target }) => {
    setState({
      ...state,
      [target.name]: target.value
    });
  }

  const login = (event) => {
    event.preventDefault();
    if (state.username === 'admin' && state.password === 'admin') {
      localStorage.setItem('USERNAME', state.username);
      setContext({
        	...store,
        	username: state.username
        });
      AppHistory.push('/');
    } else {
      setState({
        ...state,
        error: true
      });
    }
  }
  return (
    <form onSubmit={login}>
      <div className="container" id="login-container">
        <div className="text-center">
          <h1 className="heading">Login</h1>
        </div>
        <div className="row">
          <label className="col-sm-3 col-form-label" htmlFor="username">Username: </label>
          <input type="text" className="form-control col-sm-9" name="username" id="username" value={state.username} onChange={inputChanged} required />
        </div>
        <div className="row">
          <label className="col-sm-3 col-form-label" htmlFor="password">Password: </label>
          <input type="password" className="form-control col-sm-9" name="password" id="password" value={state.password} onChange={inputChanged} required/>
        </div>

        <div className={"text-center alert alert-danger " + (!state.error ? 'visibility-hidden': '')} role="alert">
            <span>Wrong Username/Password !!</span>
        </div>
        <div className="text-center" id="login-btn">
          <button type="submit" className="btn btn-info">Login</button>
        </div>
      </div>
    </form>
  );
}
